.textArea {
    padding-top: 110px;
    padding-bottom: 110px;
    margin-left: calc((100vh) * (316 / 937));
    margin-right: calc((100vh) * (494 / 937));
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;

    blockquote {
        font-family: "Courier", Courier, monospace;
        font-size: 50px;
        margin-bottom: 50px;
        width: 770px;
        text-align: center;
        quotes: "“" "”" "‘" "’";

        &:before {
            content: open-quote;
        }
        &:after {
            content: close-quote;
        }
    }

    p {
        width: 770px;
        max-height: 320px;
        margin: 0;
    }
}

@media only screen and (max-width: 916px) {
    .textArea {
        padding: 0;
        margin: calc((100vw) * (80 / 375)) calc((100vw) * (30 / 375)) calc((100vw) * (100 / 375)) calc((100vw) * (30 / 375));
        display: flex;
        flex-direction: column;
        justify-content: left;

        blockquote {
            width: auto;
            font-size: 30px;

            &:before {
            }
            &:after {
            }
        }

        p {
            width: 100%;
            max-height: fit-content;
        }
    }
}
