.imgBG{
    position: relative;
}

.imgCon{
    height: 100%;
    padding-top: 12vh;
    padding-bottom: 12vh;
    margin-right: calc((100vh) * (178 / 937));
    position: relative;

    display: flex;
    // align-items: center;
    justify-content: center;
    overflow: hidden;

    img {
        min-width: 100%;
        min-height: 100%;
        display: block;
        object-fit: cover;
    }
}

@media only screen and (max-height: 800px) {
    // .imgCon{
    //     padding-top: 0px;
    //     padding-bottom: 0px;
    // }
}

@media only screen and (max-width: $screenS) {
    .imgCon{
        width: 100%;
        padding: 0px calc((100vw) * (15 / 375)) 0px calc((100vw) * (15 / 375));
        margin: 0 0 calc((100vw) * (20 / 375)) 0;
        position: relative;
    
        display: flex;
        // align-items: center;
        justify-content: center;
        overflow: hidden;

        img {
            min-height: fit-content;
            max-height: 100%;
        }
    }
}