.cardWork {
    // height: calc(100% - 220px);
    // margin-top: 110px;
    height: calc(100% - 24vh);
    // padding-right: calc(227 / 306 * 100%);
    // padding-right: 100%;
    margin-top: 12vh;
    margin-right: calc((100vh) * (178 / 937));
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .info {
        height: 100%;
        padding-left: 80px;
        padding-right: 80px;
        position: relative;
        z-index: 1;
        top: 0;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .title {
            display: flex;
            flex-direction: column;
            align-items: center;

            .workNr {
                top: calc((100vh) * (-47 / 937));
                left: calc((100vh) * (-194 / 937));
                position: absolute;
                font-weight: bold;
                font-size: calc((100vh) * (220 / 937));
                z-index: 1;
                letter-spacing: -20px;
                cursor: default;
            }

            h1 {
                font-size: 40px;
                margin: 0;
                width: 100%;
                text-align: center;
            }
        }
    }

    .overlay {
        position: absolute;
        top: 0;
        z-index: -1;
        height: 100%;
        width: 100%;
        opacity: 0.8;
    }

    .img {
        position: absolute;
        top: 0;
        z-index: -2;
        height: 100%;
        width: 100%;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            transition-timing-function: ease-in-out;
            transition-duration: 0.4s;
            transform: rotate(0deg);

            min-width: 110%;
            min-height: 110%;
            display: block;
            object-fit: cover;
            filter: grayscale(100%);
        }
    }

    &:hover {
        .img {
            img {
                transition-duration: 0.4s;
                transition-timing-function: ease-out;
                min-width: 115%;
                min-height: 115%;
                // transform: rotate(-1deg);
            }
        }
    }
}

@media only screen and (max-width: $screenS) {
    .cardWork {
        height: 100%;
        width: calc(100% - (60 / 375 * 100vw));
        margin: calc((100vw) * (30 / 375)) calc((100vw) * (30 / 375)) calc((100vw) * (20 / 375)) calc((100vw) * (30 / 375));
        padding: 100px 10px 100px 10px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .info {
            height: 100%;
            padding-left: calc((100vw) * (80 / 375));
            padding-right: calc((100vw) * (80 / 375));
            position: relative;
            z-index: 1;
            top: 0;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .title {
                .workNr {
                    top: calc(-107px + (-15vw * (220 / 937)));
                    left: calc((100vw) * (-141 / 375));
                    position: absolute;
                    font-weight: bold;
                    font-size: calc((100vw) * (220 / 937));
                    z-index: 1;
                    letter-spacing: -10px;
                    cursor: default;
                }

                h1 {
                    font-size: 30px;
                }
            }
        }

        .overlay {
        }

        .img {
            img {
            }
        }

        &:hover {
            .img {
                img {
                }
            }
        }
    }
}
