.titleCardWrapper{
    height: 100%;
    padding-top: 12vh;
    padding-bottom: 12vh;
    padding-left: 158px;
    padding-right: 158px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;

    .workNr{
        top: -94px;
        left: -135px;
        position: absolute;
        z-index: -1;
        font-weight: bold;
        font-size: 440px;
        letter-spacing: -50px;
    }

    .titleCard{

        h1{
            font-weight: bold;
            font-size: 60px;
            margin-bottom: -25px;
        }
    
        .shortInfo{
            .row{
                display: flex;
                flex-direction: row;
                margin: 0;
    
                .title{
                    font-weight: bold;
                    font-family: 'Courier', Courier, monospace;
                    font-size: 20px;
                    padding-right: 10px;
                    line-height: 1.2;
    
                    &:after{
                        content: ":";
                    }
                }

                .info{
                    font-family: 'Courier', Courier, monospace;
                    font-size: 20px;
                    padding-right: 10px;
                    line-height: 1.2;
                }

                a{
                    font-family: 'Courier', Courier, monospace;
                    padding-right: 10px;
                    line-height: 1.2;
                    font-size: 22px;
                    color: $black;
                    position: static;
                }
            }

        }
            
        .paragraph{
            position: relative;
            max-height: 320px;
            width: 770px;
            left: 158px;
            top: 50px;
            margin: 0;
            margin-right: 158px;
            // background-color: aquamarine;
        }

        .col3{
            width: 454px;
        }

        a{
            font-family: 'Courier', Courier, monospace;
            padding-right: 10px;
            line-height: 1.5em;
            font-size: 18px;
            color: $grey;
            transition-duration: 0.2s;
            position: absolute;
            bottom: 12vh;
    
            &:hover{
                color: $black;
                text-decoration: underline;
                transition-duration: 0.2s;
            }
        }

        .cv{
            &:before{
                content: none;
            }
        }
    }
}

@media only screen and (max-width: $screenS) {
    .titleCardWrapper {
        margin: 0 0 calc((100vw) * (30 / 375)) 0;
        padding: 0 calc((100vw) * (30 / 375)) 0 calc((100vw) * (30 / 375));
        width: 100vw;

        .workNr{
            top: -40px;
            left: -60px;
            font-size: 200px;
            letter-spacing: -25px;
        }
    
        .titleCard{
            margin-top: calc((100vw) * (30 / 375));

            h1{
                font-size: 43px;
            }

            .shortInfo{
                .row{
                    .title{
                        margin-bottom: 10px;
                        &:after{
                        }
                    }
    
                    .info{
                        margin-bottom: 10px;
                    }
    
                    a{
                        margin-bottom: 10px;
                    }
                }
            }
                
            .paragraph{
                position: relative;
                max-height: 320px;
                width: 100%;
                left: 0;
                top: 50px;
                margin: 0;
                margin-bottom: 100px;
                // background-color: aquamarine;
            }

            .col3{
                width: 100%;
            }
    
            .previous{
                display: none;

                &:hover{
                }
            }
    
            .cv{
                position: static;
                &:before{
                }
            }
        }
    }
}