form {
    display: flex;
    flex-direction: column;
    margin: 0;

    label {
        margin-bottom: 1.5em;
        display: flex;
        flex-direction: column;
        width: 75%;

        p {
            font-family: "Courier", Courier, monospace;
            font-size: 25px;
            line-height: 1.2;
            margin: 0;
            // font-weight: bold;

            &:after {
                content: ":";
            }
        }

        input, textarea {
            border: none;
            border-radius: 0;
            width: 612px;
        }
    }

    #formButton{
        background-color: $lightgrey;

        &:hover{
            margin-left: 6.5px;
        }
    }
}

@media only screen and (max-width: $screenS) {
    form {
        padding: 0;
    
        label {
            width: 100%;
    
            p {
                &:after {
                }
            }
    
            input, textarea {
                width: 100%;
            }
        }
    
        #formButton{
            &:hover{
            }
        }
    }
}