.endCard{
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 158px;
    padding-right: 158px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: $lightgrey;
    height: 100%;

    .col4{
        width: 612px;
        h1{
            font-size: 50px;
            margin-bottom: -10px;
        }
    
        a{
            font-family: 'Courier', Courier, monospace;
            padding-right: 10px;
            line-height: 1.5em;
            font-size: 22px;
            color: $grey;
            transition-duration: 0.2s;
    
            // &:before{
            //     content: "→";
            //     padding-right: 10px;
            //     text-decoration: none;
            // }
    
            &:hover{
                color: $black;
                text-decoration: underline;
                transition-duration: 0.2s;
            }
        }
            
        p{
            max-height: 320px;
            margin: 0;
            color: $grey;
    
        }
        
        .footer{
            text-align: right;
        }
    
        @import "contactForm.scss";
    }


}

@media only screen and (max-width: $screenS) {
    .endCard{
        padding: 0 calc((100vw) * (30 / 375)) 0 calc((100vw) * (30 / 375));
        margin: calc((100vw) * (30 / 375)) 0 0 0;
        min-height: 400px;
        justify-content: center;

        .col4{
            width: 100%;
            padding: calc((100vw) * (50 / 375)) 0px calc((100vw) * (50 / 375)) 0px;
        }
    
        h1{
            font-size: 30px;
            margin-bottom: -10px;
        }
    
        a{ 
            &:hover{
            }
        }
            
        p{
        }
        
        .footer{
        }
    }
}