$white: #ffffff;
$offwhite: #fdfdfd;
$black: #141414;
$lightgrey: #f4f3ff;
$grey: #808080;

$mainColor: #ffd5f9;
$workColor01: #fff7aa;
$workColor02: #d5ecff;
$workColor03: #79c273;
$workColor04: #6d8dbc;
$workColor05: #ffd5f9;

$screenS: 916px;
$screenM: 916px;
$screenL: 916px;

* {
  margin: 0;
  padding: 0;
  color: $black;
}

h1 {
  font-family: Courier, monospace;
}

p {
  font-family: Helvetica;
  line-height: 1.5;
  font-size: 18px;
}

.italic{
  font-style: italic;
}

.bar {
  height: 10px;
  width: 80px;
  left: -20px;
  margin-top: 40px;
  margin-bottom: 40px;
  background-color: $black;
  position: relative;
}

.barcenter {
  left: 0;
}

.buttonBox{
  height: 50px;
  display: flex;
  align-items: center;
  
  .button {
    margin: 0;
    padding: 10px 40px 10px 40px;
    max-width: max-content;
  
    border-color: $grey;
    border-width: 4px;
    border-style: solid;
    border-radius: 0;
  
    text-transform: lowercase;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 20px;
    color: $grey;
  
    transition-duration: 0.2s;
  
    &:hover {
      color: $black;
      border-color: $black;
      text-decoration: none;
      transition-duration: 0.2s;
      padding: 8.5px 34px 8.5px 34px;
      margin: 1.5px;
      border-radius: 0;
    }
  }

  .workColor01 {
    color: $workColor01;
    border-color: $workColor01;
  
    &:hover {
      color: $workColor01;
      border-color: $workColor01;
    }
  }
  
  .workColor02 {
    color: $workColor02;
    border-color: $workColor02;
  
    &:hover {
      color: $workColor02;
      border-color: $workColor02;
    }
  }
  
  .workColor03 {
    color: $workColor03;
    border-color: $workColor03;
  
    &:hover {
      color: $workColor03;
      border-color: $workColor03;
    }
  }
  
  .workColor04 {
    color: $workColor04;
    border-color: $workColor04;
  
    &:hover {
      color: $workColor04;
      border-color: $workColor04;
    }
  }
  
  .workColor05 {
    color: $workColor05;
    border-color: $workColor05;
  
    &:hover {
      color: $workColor05;
      border-color: $workColor05;
    }
  }
}

.pagesWrapper {
  height: 100vw;
  width: calc(100vh - 80px);
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
  top: 0px;

  // background-color: $black;

  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

//Columns
.col3 {
  // width: 454px;
  width: calc((80vh - 80px) * (454 / 632));
}


.col4 {
  width: 612px;
  width: calc((80vh - 80px) * (612 / 632));
}

.col5 {
  width: 770px;
  width: calc((80vh - 80px) * (770 / 632));
}

.col6 {
  width: 928px;
  width: calc((80vh - 80px) * (928 / 632));
}

.col7 {
  width: 1086px;
  width: calc((80vh - 80px) * (1086 / 632));
}

.col8 {
  width: 1244px;
  width: calc((80vh - 80px) * (1244 / 632));
}

.col9 {
  width: 1402px;
  width: calc((80vh - 80px) * (1402 / 632));
}

.col10 {
  width: 1560px;
  width: calc((80vh - 80px) * (1560 / 632));
}

.bg {
  top: 0;
  left: calc((100vh) * (316 / 937));
  position: absolute;
  height: 100%;
  z-index: -1;
}

//Colors & BG
.mainColor {
  color: $mainColor;
}

.workColor01 {
  color: $workColor01;
  border-color: $workColor01;

  &:hover {
    color: $workColor01;
    border-color: $workColor01;
  }
}

.workColor02 {
  color: $workColor02;
  border-color: $workColor02;

  &:hover {
    color: $workColor02;
    border-color: $workColor02;
  }
}

.workColor03 {
  color: $workColor03;
  border-color: $workColor03;

  &:hover {
    color: $workColor03;
    border-color: $workColor03;
  }
}

.workColor04 {
  color: $workColor04;
  border-color: $workColor04;

  &:hover {
    color: $workColor04;
    border-color: $workColor04;
  }
}

.workColor05 {
  color: $workColor05;
  border-color: $workColor05;

  &:hover {
    color: $workColor05;
    border-color: $workColor05;
  }
}

.mainBG {
  background-color: $mainColor;
}

.blackBG {
  background-color: $black;
}

.workBG01 {
  background-color: $workColor01;
}

.workBG02 {
  background-color: $workColor02;
}

.workBG03 {
  background-color: $workColor03;
}

.workBG04 {
  background-color: $workColor04;
}

.workBG05 {
  background-color: $workColor05;
}


@media only screen and (max-width: $screenS) {
  .pagesWrapper {
    height: auto;
    width: 100vw;
    transform: rotate(0) translateX(0);
    transform-origin: top left;
    overflow-y: scroll;
    overflow-x: scroll;
    position: static;
    top: 0px;
    background-color: $black;

    scrollbar-width: none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
      display: none;
      // z-index: 5;
    }
  }

  .col3, .col4, .col5, .col6, .col7 {
    width: 100%;
  }

  .bg {
    top: calc((100vw) * (100 / 375));
    left: 0;
    position: absolute;
    width: 100%;
    z-index: -1;
  }
}
