header {
    div {
        width: 100vw;
        height: 80px;
        background-color: $white;
        padding-left: 22px;
        padding-right: 22px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .logoL {
            display: flex;
            flex-wrap: nowrap;
            // justify-content: Left;
            padding-left: calc((100vw) * (158 / 1920));

            h1 {
                margin: 0;
                font-size: 35px;
                display: block;

                a {
                    color: $black;
                }

                a:hover {
                    text-decoration: none;
                    color: $black;
                }
            }
        }

        .logoS {
            display: none;
        }

        .nav {
            display: flex;
            flex-wrap: nowrap;
            justify-content: end;
            padding-right: calc((100vw) * (158 / 1920));
            list-style: none;
            flex-direction: row;
            margin: 0;

            div {
                padding: 0;
                width: 70px;
                height: 80px;
                display: flex;
                justify-content: center;
                align-items: center;
                // background-color: $lightgrey;
                margin-left: calc((100vw) * (60 / 1920));

                li a {
                    display: inline;
                    font-family: Arial, Helvetica, sans-serif;
                    color: $grey;
                    transition-duration: 0.2s;
                    font-size: 18px;

                    &:hover {
                        text-decoration: none;
                        color: $black;
                        transition-duration: 0.2s;
                    }
                }

                .navActive {
                    font-weight: bold;
                    color: $black;
                }
            }

            .left{
                margin-left: 0;
            }
        }

        .mobileNavOpen,
        .mobileNavClosed {
            transition-duration: 1s;
            display: none;
        }

        .hamburgerIcon {
            display: none;

            &:focus {
                outline: none;
            }
        }
    }
}

@media only screen and (max-width: $screenS) {
    header {
        div {
            width: 100vw;
            

            .logoL {
                display: none;
            }

            .logoS {
                width: max-content;
                display: flex;
                flex-wrap: nowrap;
                padding-left: calc((100vw) * (18 / 375));

                h1 {
                    margin: 0;
                    font-size: 35px;
                    display: block;

                    a {
                        color: $black;
                    }

                    a:hover {
                        text-decoration: none;
                        color: $black;
                    }
                }
            }

            .nav {
                display: none;
            }

            .mobileNavOpen {
                display: block;
                opacity: 0;
                transition: opacity 3000ms;
                top: 80px;
                left: 0;
                background-color: $white;
                width: 100vw;
                justify-content: end;
                list-style: none;
                margin: 0;
                position: absolute;
                z-index: 10;
                position: fixed;
                height: calc(100vh - 80px);

                div {
                    padding: 0;
                    margin: 0;
                    height: 60px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    // background-color: $lightgrey;

                    li a {
                        display: inline;
                        font-family: Arial, Helvetica, sans-serif;
                        color: $grey;
                        transition-duration: 0.2s;
                        font-size: 18px;

                        &:hover {
                            text-decoration: none;
                            color: $black;
                            transition-duration: 0.2s;
                        }
                    }

                    .navActive {
                        font-weight: bold;
                        color: $black;
                    }
                }
            }

            .mobileNavOpenAnimated {
                opacity: 1;
            }

            .hamburgerIcon {
                display: block;
                margin: 0 calc((100vw) * (18 / 375)) 0 0;
                border: 0;
                cursor: pointer;

                .bar1,
                .bar2,
                .bar3 {
                    width: 30px;
                    height: 5px;
                    background-color: #333;
                    margin: 5px 0;
                }
            }
        }

        .fixed{
            position: fixed;
            top:0;
            z-index: 1;
        }

        .normal{
            position: static;
        }
    }
}
