.card{
    height: calc(100% - 24vh);
    margin: 12vh calc((100vh) * (178 / 937)) 12vh 0;
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: $lightgrey;
    border-radius: 0;
    border: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .title{
        display: flex;
        flex-direction: column;
        align-items: center;

        h1{
            font-size: 40px;
            margin: 0;
            width: 100%;
            text-align: center;
        }
    }
}

@media only screen and (max-width: $screenS) {
    .card{
        height: 100%;
        width: calc(100% - (60 / 375 * 100vw));
        margin: calc((100vw) * (30 / 375)) calc((100vw) * (30 / 375)) calc((100vw) * (50 / 375)) calc((100vw) * (30 / 375));
        padding: 100px calc((100vw) * (20 / 375)) 100px calc((100vw) * (20 / 375));
        background-color: $lightgrey;
        border-radius: 0;
        border: none;
    
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    
        .title{
            display: flex;
            flex-direction: column;
            align-items: center;
    
            h1{
                font-size: 40px;
                margin: 0;
                width: 100%;
                text-align: center;
            }
        }
    }
  }